@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Karla:wght@400;500;600&family=Nunito:wght@200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    display: flex;
    background-color: black;
    justify-content: center;
    align-items: center;
    font-family: Karla;
}

.quiz-screen {
    background-color: purple;
    width: 100%;
    padding-left: 75px;
    padding-right: 75px;
    padding-top: 40px;
    padding-bottom: 25px;
    width: 550px;
    min-height: 550px;
    background-color: white;

    h2 {
        font-size: 16px;
        margin-top: 30px;
    }
    .option {
        border-radius: 10px;
        padding: 5px 10px;
        margin-left: 1rem;
        border: solid 1px #4d5b9e;
        font-family: Inter;
        font-weight: 500;
        font-size: 10.24px;
        margin-top: 14px;
        cursor: pointer;
        // white-space: initial;
        display: inline-block;
        // width: ;
    }
    .answers {
        margin-top: 14px;
    }
    .selected {
        background-color: #4d5b9e;
        color: white;
    }
}
.welcome-screen {
    background-color: white;
    width: 550px;
    min-height: 550px;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;

    p {
        margin-top: 10px;
    }
    h2 {
        color: #293264;
        font-size: 12px;
        font-family: Karla;
    }
}
button {
    display: block;
    margin-top: 10px;
    padding: 5px 10px;
    background-color: #4d5b9e;
    border-radius: 5px;
    border: none;
    color: white;
    cursor: pointer;
    margin: 20px auto;
}
.answer {
    background-color: #94d7a2;
    color: black;
    border-color: none;
}

.wrong-answer {
    background-color: #f8bcbc;
    color: #293264;
    border-color: none;
}
